	module.exports = {"data":{"markdownRemark":{"html":"<div class=\"post-container\">\n<picture>\n<source media=\"(min-width: 620px)\" srcset=\"/static/2t.jpg\">\n<img class=\"pic\" src=\"/static/2m.jpg\" alt=\"12\">\n</picture>\n</div>\n<div class = \"int-container\">\n<h1>Мини-интервью в рамках рубрики \"Мама недели\"</h1>\n<h2>Мы решили разнообразить нашу страничку и ввести рубрику «Мама недели», где будем делиться с вами историями мам. Сегодня мы откровенно поговорим с героиней недели - Аллой Удальцовой.</h2>\n<p><strong>Mommys band - Алла, тебе сейчас 24. За последние 5 лет в твоей жизни произошло много событий, которые изменили тебя не только внешне (ты стала взрослее, мудрее, ушли твои милые щечки, а взгляд стал строже и глубже), но и внутренне. Я бы хотела, чтобы твоя автобиография стала для кого-то вдохновением и желанием жить и жить счастливо не смотря ни на что. Чуть больше четырёх лет назад, когда родился Дима, ты потеряла мужа. Это было внезапно, больно. Как ты выбралась? Что помогло преодолеть весь этот ужас и жить дальше?</strong></p>\n<p><strong>Алла</strong> - Честно, это было ужасно. Я не помню свои эмоции поминутно, да и не могу сказать, что что-то четко помню. Все было как в тумане. Мне хотелось куда-то бежать, кричать, что-то делать. Мне помогло пережить первый шок то, что я сама занималась организацией похорон от «а» до «я». Я сделала так, как должно было быть и процесс организации очень мне помог в первые дни. Я не была один на один со своими мыслями. Могу сказать, что я основательно отошла от всей этой истории года 2 спустя. Меня очень поддерживали мои родители. Если бы не мама с папой, я даже не знаю, что было бы. Спустя 2 года у меня начался период переоценки ценностей, и я избавилась окончательно от образа жертвы. Я осознала, что по сути ко мне и моему ребенку это не имеет ни какого отношения. Вся эта история началась за долго до наших отношений и рождения Димы. В течении года я разобралась с тараканами в голове, начала принимать себя такую, какая я есть, прокачивала себя. В итоге очень сильно изменилось мое окружение. Я, можно сказать, избавилась от людей с которыми мне не комфортно, которые ищут выгоду, которые сплетничают за спиной, а в глаза сказать не могут. В результате я выросла и мне уже не интересно с людьми, которые не растут и не развиваются. В итоге тот ужас который свалился на мои плечи, заставил меня заново взглянуть на себя, появилась та Алла, которая есть сейчас. Но нет предела совершенству. Нужно работать и работать над собой. Иногда дать себе пендель, иногда остаться наедине со своими мыслями...</p>\n<picture>\n<source media=\"(min-width: 620px)\" srcset=\"/static/3t.jpg\">\n<img class=\"pic\" src=\"/static/3m.jpg\" alt=\"12\">\n</picture>\n<p><strong>Mommys band - Это правда, что ты сама делала ремонт?</strong></p>\n<p><strong>Алла</strong> - У меня была, конечно же, бригада из 2х человек. Но я делала многое сама. Начиная выбором обоев, заканчивая поднятием сторойматериалов в квартиру. Потом сама ездила покупала мебель и на своей машине привозила домой, поднимала все тоже сама (в основном я ездила по магазинам вечером или ночью, когда Дима спал). Я сама собрала всю мебель в комнате Димы. И стоит до сих пор! Папа в тот период сломал себе один позвонок и не мог мне помочь, поскольку лежал. Но эта ситуация преподнесла мне еще один урок в жизни - не верь, люди любят чесать языком. </p>\n<picture>\n<source media=\"(min-width: 620px)\" srcset=\"/static/4t.jpg\">\n<img class=\"pic\" src=\"/static/4m.jpg\" alt=\"12\">\n</picture>\n<p><strong>Mommys band - Я помню тебя совсем юной девчушкой, с щечками, с сумкой Louis Vuitton и в лабутенах. Ты всегда проявляла интерес к миру моды. Но в этом году ты меня приятно удивила и стала сотрудницей Vogue Russia! Расскажи, как ты туда устроилась и есть ли шанс у простых смертных попасть в журнал мечты, модничать и работать в Condé Nast?</strong></p>\n<p><strong>Алла</strong> - Не буду скрывать, я всегда об этом мечтала. Журнал Vogue - это же бомба. Я собирала номера, привозила из путешествий иностранные издания. Из Америки везла два толстенных номера! Попала очень просто. Выложила свое резюме на hh.ru. Меня пригласили на собеседование. Я даже не знала какой журнал, пока не подошла к концу первая встреча с очаровательной сотрудницей HR-отдела. Когда я услышала, что Vogue, я сразу же сказала себе - это мое место. Так и получилось. Я была самой настоящей смертной. За меня никто не просил, никто не помогал. Все сама.</p>\n<picture>\n<source media=\"(min-width: 620px)\" srcset=\"/static/5t.jpg\">\n<img class=\"pic\" src=\"/static/5m.jpg\" alt=\"12\">\n</picture>\n<p><strong>Mommys band - Алла, какого это воспитывать сына? У тебя есть какие-то методы и секреты воспитания пацана?</strong></p>\n<p><strong>Алла</strong> - Это интересно. Я не читала никаких книг про детей никогда. Мой подход - воспитай себя. Мы с Димой не просто мама и сын, мы друзья. Мы любим дурачиться, смеяться, щекотать друг друга и прыгать на кровати. Я позволяю Диме делать все то, что я делала в детстве и то, что я хотела делать. Я никогда не обращалась к нему, как к ребенку и никогда не сюсюкаюсь. Люблю Диму обнимать и целовать. Он от этого не в восторге, стоит и ждёт, пока я нацелую и наобнимаю его и говорит: «Мама, может хвалить обниматься, можно я пойду железную дорого строить».\nНам такой вариант диалога подходит. Мы счастливы. </p>\n<picture>\n<source media=\"(min-width: 620px)\" srcset=\"/static/6t.jpg\">\n<img class=\"pic\" src=\"/static/6m.jpg\" alt=\"12\">\n</picture>\n<p><strong>Mommys band - Дима ходит в садик? Бытует мнение, точнее некоторые мамы убеждены, что сады это зло. Якобы в садах дети дерутся, становятся агрессивней, часто болеют. Я считаю, что это первая и чуть ли не главная школа жизни. Но надо очень много говорить с ребёнком, чтобы он рассказывал все, что происходит в садике.</strong></p>\n<p><strong>Алла</strong> - Дима ходит в сад. Я абсолютно с тобой согласна, детский сад - это школа жизни. Болеет частенько, но лучше пусть он переболеет в саду, чем в школе. Я стараюсь относиться к его ОРВИ спокойно, а свои переживания оставлять при себе.\nНаш первый сад подкачал, сейчас я перевела его в другой. Тьфу-тьфу, надеюсь, все будет хорошо. У нас с Димой есть диалог. Я считаю, что это самое важное. Он рассказывает мне все свои секреты. Даже все те секреты, инициатором которых был сам Дима. Я люблю его слушать, он мне напоминает меня. Он знает, что мне он может рассказать все и это останется только между нами.</p>\n<picture>\n<source media=\"(min-width: 620px)\" srcset=\"/static/8t.jpg\">\n<img class=\"pic\" src=\"/static/8m.jpg\" alt=\"12\">\n</picture>\n<p><strong>Mommys band - Ты невероятно красивая. Расскажи о своём уходе за лицом, телом и волосах.</strong> </p>\n<p><strong>Алла</strong> - Благодарю. Лицо. Все очень просто, кожу хорошо очищаем, затем увлажняем. У меня уход Sensai. Он дорогой, но очень экономичный и хорошо работает. Часто меня можно встретить без косметики - использую только дневной крем с spf. Если есть косметика, то это минимум пудры, тени, туш, карандаш для бровей, румяна. Люблю разные увлажняющие маски, стараюсь раз в месяц делать массаж лица.\nС кожей на теле дела обстоят похуже,она довольно сухая, сейчас перешла на масла. Чередую миндальное и кокосовое. Люблю массаж.\nВолосы стараюсь максимально увлажнять. Маски делаю 2 раза в неделю. В поисках идеальных средств для волос, но на данный момент мой фаворит moroccanoil.\nСлежу за питанием, стараюсь не злоупотреблять фастфудами (хотя признаюсь, люблю это дело), стараюсь есть печёное, отварное и не употребляю коровье молоко. Заменяю кокосовым и миндальным. Обожаю салаты. </p>\n<picture>\n<source media=\"(min-width: 620px)\" srcset=\"/static/9t.jpg\">\n<img class=\"pic\" src=\"/static/9m.jpg\" alt=\"12\">\n</picture>\n<p><strong>Mommys band - Что можешь посоветовать женщинам, которые остались наедине со своими проблемами, да ещё и с детьми на руках?</strong> </p>\n<p><strong>Алла</strong> - Не опускать руки. Есть выход абсолютно из любой ситуации. Просто встаешь утром и вперед. Нет отговорк, нет жалости к себе, нет оправданий и каких-то причин. Есть только желание или не желание. А если есть желание развиваться и что-то делать, то все обязательно получится. Проверено.</p>\n</div>","frontmatter":{"date":"July 12, 2017","path":"/alla","title":"Съемка c Аллой Удальцовой"}}},"pathContext":{}}

